import { Component } from '@angular/core';
import { empty } from 'rxjs';
import { logentry } from './logentry';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { LogService } from './log.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  public logs: logentry[] = [];

  public emptyLog: logentry = <logentry>{};
  constructor(private logService: LogService) {
    this.getLogs();
  }

  public log: logentry = <logentry>{};
  modalType = '';
  editIndex = -1;

  getLogs() {
    this.logService.getLogs().subscribe(
      (response: any) => {
        // console.log(response);
        this.logs = JSON.parse(response._body);
      },
      error => console.log(error)
    );
  }

  addOrEditLog() {
    if (this.modalType === 'add') {
      // this.logs.unshift(this.log);

      this.logService.addLog(this.log).subscribe(
        response => {
          // console.log(response);
          this.getLogs();
        },
        error => console.log(error)
      );
    } else {
       // this.logs[this.editIndex] = this.log;

       this.logService.updateLog(this.log).subscribe(
         response => {
            console.log(response);
            this.getLogs();
         // this.logs[this.editIndex] = this.log;
         },
         error => console.log(error)
       );
    }
    this.modalType = '';
  }

  showModal(type, index = 0) {
    this.modalType = type;
    if (type === 'edit') {
      this.log = this.logs[index];
      this.editIndex = index;
    } else if (type === 'add') {
      this.log = this.emptyLog;
    }
  }

  hideModal() {
    this.modalType = '';
    // this.log = <logentry>{};
  }
  onDelete(log) {
    console.log(log);
    this.logService.deleteLog(log).subscribe(
    response => {
    // console.log(response);
    this.getLogs();
    },
    error => console.log(error)
    );
    }
}
